<template>
  <div id="app">
    <link rel="stylesheet" type="text/css" href="static/style.css"/>
    <header>
      <a href="/">
        <img type="image/png" id="img-banner" src="/static/banner.png" alt="banner">
      </a>
      <nav class="nav nav-pills nav-fill">
        <a class="nav-link" href="user/signup">
          <button type="button" class="btn btn-secondary">
            Share music now
          </button>
        </a>
        <a class="nav-link" href="/about">
          <button type="button" class="btn btn-secondary">
            About Hose
          </button>
        </a>
        <router-link to="/legals/">
          <button type="button" class="btn btn-secondary">
            Legal Stuff
          </button>
        </router-link>
        <!--<a class="nav-link" href="/legals/">-->
          <!--<button type="button" class="btn btn-secondary">-->
            <!--Legal stuff-->
          <!--</button>-->
        <!--</a>-->
      </nav>
    </header>
    <main>
      <div>
        <p style="text-align: center">You're not currenly logged in</p>
        <div class="container text-center" id="not-logged-buttons">
          <button id="login-button" type="button"
                  class="btn btn-primary"
                  v-if="!showLogin" @click="showLogin=!showLogin">Login</button>
          <form v-if="showLogin" method="POST" @submit="sendLoginInfo">
            <div class="form-group">
              <label for="username">Username</label>
              <input type="text" class="form-control" id="username" placeholder="Username">
                <small id="emailHelp" class="form-text text-muted">
                  We'll never share your email with anyone else.</small>
              <div class="form-group">
                <label for="password">Password</label>
                <input type="password" class="form-control" id="password" placeholder="Password">
              </div>
            </div>
            <button type="submit" class="btn btn-primary">Login</button>
          </form>
          <hr/>
          <a class="col" href="user/signup">
            <button id="signup-button" type="button" class="btn btn-primary">Register</button>
          </a>
        </div>
      </div>
    </main>
    <footer>
      <div class="container">
        <div class="row">
          <div class="col" style="text-align: left">
            <p>Github: <a href="https://github.com/Gnonpi/hose-sound/tree/master">hose-sound</a></p>
            <p>Email: <a href="legnonpi@gmail.com">Gnonpi</a></p>
          </div>
          <div class="col" style="text-align: right">
            <p>© Copyright Gnonpi 2019</p>
          </div>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
const urlBackend = 'http://127.0.0.1:8000'

export default {
  name: 'HomePage',
  data: function () {
    return {
      showLogin: false
    }
  },
  methods: {
    sendLoginInfo: function (e) {
      console.log(JSON.stringify(e))
      this.axios.post(urlBackend + '/login/')
        .then(response => {
          console.log(response)
        })
        .catch(error => {
          console.error(JSON.stringify(error, null, 2))
          console.error(error)
        })
    }
  }
}
</script>

<style scoped>

</style>



